@import 'fontawesome/font-awesome.min.css';
@import url('https://fonts.googleapis.com/css?family=Open+Sans');

$marine: #003874;
$orange: #f47421;
$cyan: #7fc6c3;
$pink: #e094bf;
$blue: #255791;

$lightblue: rgba(77,199,196, 1);

$black: #222;
$white: #ddd;

.shardbg, body {
	background-attachment: fixed;
	background-position: left bottom;
	background-size: contain;
	background-repeat: no-repeat;
}

body{
	font-family: 'Open Sans', sans-serif;
	background-image: url('imgs/shardsSmall.png');
	color: $black;
	margin: 0;
}

.shardbg {
	z-index: -10;
	position: fixed;
	background-image: linear-gradient(0, #FFEEE2, #fff);
	width: 100vw;
	height: 100vh;
}

section {
	min-height: 100vh;
}

.content {
	height: calc(100vh - 3em);

	h1 {
		margin-top: 0;
		margin-left: 0.83em;
		padding-top: 0.83em;
	}
}

.goto{
	height: 3em;
	background-color: $blue;
	text-align: center;

	span{
		display: block;
		margin: 0 auto;
		height: inherit;
		color: white;
		text-align: center;
		font-weight: bold;
		line-height: 3em;

		.fa {
			float: left;
			margin-left: .5em;
		}
	}
}


.lead {
	p {
		margin: .5em 10%;
		font-weight: bold;
		line-height: 1.5em;
	}

	.title {
		display: block;
		margin: 0 auto;
		padding: 0.6em;
		padding-left: 1em;
		height: 2em;
		width: 80%;
		border-radius: 1.5em;
		background-color: $blue;
		color: white;
		text-align: center;
		font-weight: bold;
		line-height: 2em;

		.fa {
			float: left;
			margin-right: -1em;
		}
	}
}

.responsive {
	max-width: 50vw;
}

.center {
	display: block;
	margin: 0 auto;
}

.slide-content{
	background: linear-gradient(0, rgba(10,10,10,0.4), rgba(0,0,0,0));
}

.img-slide{
	width: 30vmin;
}
